/**/
.container {
  background-color: #f7f7f7;
}
.item {
  background-color: #a2cdfa;
  border: 1px solid #4390e1;
  border-radius: 10px;
}
/* Flexbox. */
.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.item {
  height: 100px;
  margin: 0 0 2% 0;
  flex-grow: 0;
  flex-basis: 32%;
}
